

import Vue from 'vue'

import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

import App from './App'
import router from './router/index'
import axiosUtil from './axios/index'

Vue.use(VueRouter)


FastClick.attach(document.body)

Vue.config.productionTip = false

Vue.use(ToastPlugin)
Vue.use(LoadingPlugin)
Vue.use(ConfirmPlugin)

Vue.prototype.$axios = axiosUtil

router.beforeEach((to, from, next) => {
window.document.title = to.meta.title
next()
})

/* eslint-disable no-new */
new Vue({
router,
render: h => h(App)
}).$mount('#app-box')



